	padding: 10px 20px;
	background-color: transparent;
	color: white;
	font-size: 20px;
	cursor: pointer;
  }
  
  .styles_navbar__2bLRx button:first-child {
	margin-left: auto; /* Dodaj tę linię, aby przesunąć pierwszy przycisk na prawo */
