.navbar {
	display: flex;
	align-items: center;
	justify-content: flex-end; /* Zmiana wartości na flex-end */
	background-color: #065c4f;
	padding: 10px;
	color: white;
  }
  
  .navbar h1 {
	font-size: 35px;
	margin: 0;
	margin-right: auto;
  }
  
  .navbar button {
	border: none;
	outline: none;
	padding: 10px 20px;
	background-color: transparent;
	color: white;
	font-size: 20px;
	cursor: pointer;
  }
  
  .navbar button:first-child {
	margin-left: auto; /* Dodaj tę linię, aby przesunąć pierwszy przycisk na prawo */
  }
  
  .navbar button:hover {
	background-color: rgba(255, 255, 255, 0.2);
	transition: background-color 0.3s ease;
  }

 /* Przykładowe style dla responsywności */
