  /* Responsywność dla ekranów o szerokości mniejszej niż 768px */
  @media (max-width: 768px) {
    .table {
      font-size: 14px; /* Decrease font size for smaller screens */
    }
  
    th,
    td {
      padding: 5px; /* Adjust cell padding for smaller screens */
    }
	.heading-1 {
